.head {
    padding: 20px 0;
    color: #fff;
}

.logo h1 {
    font-size: 35px;
    font-weight: 900;
}

.head .icon {
    margin-left: 10px;
}

/* ---------------------------------------- Header---------------------------------------- */
header {
    background-color: rgba(255, 255, 255, 0.2);
    margin: 0 30px;
}

header ul {
    padding: 25px 20px;
}

header li {
    margin-right: 40px;
}

header ul li a {
    color: #fff;
    font-weight: 500;
    transition: 0.5s;
}

header ul li a:hover {
    color: #1eb2a6;
}

.start {
    background-color: #1eb2a6;
    padding: 25px 70px;
    color: #fff;
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
}

.toggle {
    display: none;
}

@media screen and (max-width:768px) {
    .start {
        clip-path: none;
    }

    header {
        background-color: #1eb2a6;
        /* margin: 0; */
        margin-left: 80px;
        position: relative;
    }

    header ul li {
        margin-bottom: 10px;
    }

    header ul.flexSB {
        display: none;
    }

    .toggle {
        display: block;
        background: none;
        color: #fff;
        font-size: 30px;
        position: absolute;
        right: 30px;
        top: -30px;
        border: 0px;
    }

    .mobile-nav {
        position: absolute;
        top: 7vh;
        left: 0;
        width: 100%;
        background-color: #1eb2a6;
    }
}